import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router'
import { Tag, Modal, Button, Form, Select } from 'antd'

const jquery = require('jquery/dist/jquery.js')
const mathquill = require('mathquill/build/mathquill.js')

const Option = Select.Option;
const FormItem = Form.Item;

class exportModule extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // 
      args: [{
        cName: "",
        eName: "age",
      },{
        cName: "",
        eName: "length",
      },{
        cName: "",
        eName: "width",
      },{
        cName: "",
        eName: "height",
      },{
        cName: "",
        eName: "money",
      }],
      // 
      math: [{
        method: 'plus',
        name: '',
        arg: 2
      }, {
        method: 'subtract',
        name: '',
        arg: 2
      }, {
        method: 'ride',
        name: '',
        arg: 2
      }, {
        method: 'remove',
        name: '',
        arg: 2
      }],
      // 
      selected: {}
    }
    console.log(jquery);
  }

  createArg = ()=>{
    var option = this.props.form.getFieldsValue;
    var time = (new Date()).getTime();
    var obj = {
      cName: "cName"+time,
      eName: "eName"+time,
      value: option
    }
    this.state.args.push(obj);
    this.setState({
      args: this.state.args
    });
    this.props.form.resetFields();
  }

  renderTag = ()=>{
    return (
      <div>
        {
          this.state.args.map((item)=>{
            return <Tag key={item.eName}>{item.cName}</Tag>
          })
        }
      </div>
    )
  }

  renderNewTag = ()=>{
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: { span: 10 },
      wrapperCol: { span: 14 },
    };
    return (
      <div style={{marginTop:20}}>
        <FormItem {...formItemLayout} label="">
          {getFieldDecorator("math",{
            initialValue: 'plus',
            rules: [{
              required: true, message: '',
            }]
          })(
            <Select style={{ width: 200 }}>
              {
                this.state.math.map((item)=>{
                  return <Option key={item.method}>{item.name}</Option>
                })
              }
            </Select>
          )}
        </FormItem>
        <FormItem {...formItemLayout} label="">
          {getFieldDecorator("args",{
          })(
            <Select
              multiple
              style={{ width: 200 }}
            >
              {
                this.state.args.map((item)=>{
                  return <Option key={item.eName}>{item.cName}</Option>
                })
              }
            </Select>
          )}
        </FormItem>
        <Button onClick={this.createArg}></Button>
      </div>
    )
  }

  render() {
    return (
      <div>
        {
          this.renderTag()
        }
        {
          this.renderNewTag()
        }
      </div>
    )
  }
}

module.exports = connect()(Form.create()(exportModule))



